@font-face {
	font-family: 'Inter';
	font-style: normal;
	font-weight: 700;
	font-display: swap;
	src:
		url('../fonts/Inter-Bold.ttf') format('truetype'),
		url('../fonts/Inter-Bold.woff') format('woff'),
		url('../fonts/Inter-Bold.woff2') format('woff2');
}

